<template>
  <v-navigation-drawer
    id="app-drawer"
    v-model="inputValue"
    app
    dark
    floating
    persistent
    mobile-break-point="991"
    width="250"
  >
    <v-img
      :src="image"
      height="100%"
    >
      <v-layout
        class="fill-height"
        tag="v-list"
        column
      >
        <v-list-tile avatar>
          <v-list-tile-avatar
            color="white"
          >
            <a href="/">
              <v-img
                :src="logo"
                height="34"
                contain
              />
            </a>
          </v-list-tile-avatar>
          <v-list-tile-title class="title">
            <a href="/">
              湃森实验室
            </a>
          </v-list-tile-title>
        </v-list-tile>

        <v-divider/>
        <v-list-tile
          v-if="responsive"
        >
          <v-text-field
            class="purple-input search-input"
            label="Search..."
            color="purple"
          />
        </v-list-tile>
        <v-list-tile
          v-for="(link, i) in links"
          :key="i"
          :to="link.to"
          :active-class="color"
          avatar
          class="v-list-item"
        >
          <v-list-tile-action>
            <v-icon>{{ link.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-title
            v-text="link.text"
          />
        </v-list-tile>
      </v-layout>
    </v-img>
  </v-navigation-drawer>
</template>

<script>
// Utilities
import {
  mapMutations,
  mapState
} from 'vuex'

export default {
  data: () => ({
    // logo: '//www.gravatar.com/avatar/ba7eacd36a440d894441cf83d6245b38?s=150',
    logo: '',
    links: [
      {
        to: '/blog/@new',
        icon: 'mdi-pencil',
        text: '写文章'
      },
      {
        to: '/dashboard',
        icon: 'mdi-view-dashboard',
        text: '主面板'
      },
      {
        to: '/profile/posts',
        icon: 'mdi-hexagon-outline',
        text: '文章管理'
      },
      {
        to: '/profile/pages',
        icon: 'mdi-package',
        text: '页面管理'
      },
      {
        to: '/profile/tags',
        icon: 'mdi-tag-text-outline',
        text: '标签管理'
      },
      {
        to: '/profile/member',
        icon: 'mdi-account',
        text: '用户管理'
      },
      {
        to: '/profile/sys',
        icon: 'mdi-clipboard-outline',
        text: '博客管理'
      }
    ],
    responsive: false
  }),
  computed: {
    ...mapState('app', ['image', 'color']),
    inputValue: {
      get () {
        return this.$store.state.app.drawer
      },
      set (val) {
        this.setDrawer(val)
      }
    },
    items () {
      return this.$t('Layout.View.items')
    }
  },
  mounted () {
    this.onResponsiveInverted()
    window.addEventListener('resize', this.onResponsiveInverted)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResponsiveInverted)
  },
  methods: {
    ...mapMutations('app', ['setDrawer', 'toggleDrawer']),
    onResponsiveInverted () {
      if (window.innerWidth < 991) {
        this.responsive = true
      } else {
        this.responsive = false
      }
    }
  }
}
</script>

<style lang="scss">
  #app-drawer {
    .v-list__tile {
      border-radius: 4px;

      &--buy {
        margin-top: auto;
        margin-bottom: 17px;
      }
    }

    .v-image__image--contain {
      top: 9px;
      height: 60%;
    }

    .search-input {
      margin-bottom: 30px !important;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
</style>
