<template>
  <div id="FroalaEditor">
    <froala
      :config="config"
      :tag="tag"
      v-model="content" />
  </div>

</template>

<script>
// eslint-disable-next-line
import VueFroala from 'vue-froala-wysiwyg'

export default {
  name: 'FroalaEditor',
  props: {
    content: {
      type: String,
      default: ''
    },
    tag: {
      type: String,
      default: 'textarea'
    },
    config: {
      type: Object,
      default: () => {
        return {
          height: 450,
          toolbarSticky: false,
          autofocus: true,
          toolbarButtons: [
            'quote', 'bold', 'italic', 'underline', 'strikeThrough', 'subscript', 'superscript', '|',
            'fontFamily', 'fontSize', 'color', 'inlineClass', 'inlineStyle', 'paragraphStyle', 'lineHeight', '|',
            'paragraphFormat', 'align', 'formatOL', 'formatUL', 'outdent', 'indent', '-',
            'insertLink', 'insertImage', 'embedly', 'insertTable', 'emoticons', 'fontAwesome', 'insertHR', '|',
            'selectAll', 'undo', 'redo', 'clearFormatting', 'html', 'fullscreen'],
          placeholderText: '写点什么... '
        }
      }
    }
  },
  data () {
    return {
      // content: null
    }
  },
  methods: {
    listenTyping: () => {
      this.$emit('respContent')
    }
  }
}
</script>

<style>
  #FroalaEditor > div > div.fr-wrapper.show-placeholder > div:nth-child(1) {
    display: none !important;
  }
</style>
